.resume-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: fit-content;
    margin: 120px 0;
  }
  
  .resume-card {
    display: flex;
    height: 360px;
    width: 1000px;
 
  }
  
  .resume-bullets {
    box-shadow: 15px 0 9px -15px rgb(179, 79, 79);
    height: 100%;
    width: 320px;
  }
  
  .resume-bullet-details {
    flex-grow: 1;
    width: 600px;
    overflow: hidden;
    padding: 0 0 0 80px;
  }
  
  .experience-description {
    margin: 10px 0 0 0;
    text-align: justify;
  }
  
  .resume-description-text {
    font-size: 12px;
  }
  
  .resume-details-carousal {
    transition: transform 1s ease-out;
  }
  
  .bullet-container {
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
  }
  
  .bullets {
    width: 86%;
    position: relative;
    z-index: 2;
  }
  
  .bullet {
    display: flex;
    align-items: center;
    cursor: pointer;
    background-color: rgb(179, 79, 79);
    height: 40px;
    margin: 15px 0;
    padding: 0 8px;
    border-radius: 20px;
    width: 30px;
    transition: width 0.6s ease;
  }
  
  .bullet-label {
    font-size: 14px;
    white-space: nowrap;
  
  }
  
  .bullet-logo {
    height: 16px;
    margin: 0 30px 0 0;
  }
  
  .bullet-icons {
    width: 34px;
    height: 100%;
    z-index: 1;
    background-color: rgb(179, 79, 79);
    position: absolute;
  }
  
  .selected-bullet {
    color: white;
    width: 100%;
  }
  
  .resume-screen-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 360px;
    width: 100%;
  }
  
  .resume-heading {
    display: flex;
    flex-direction: column;
  }
  
  .resume-main-heading {
    position: relative;
    display: flex;
    justify-content: space-between;
  }
  
  .resume-main-heading span {
    /* color: rgb(179, 79, 79); */
    font-size: 18px;
    font-weight: bold;
  
  }
  
  .experience-description {
    max-width: 100%;
  }
  
  .heading-bullet {
    position: absolute;
    left: -30px;
    height: 15px;
    width: 15px;
    top: 5px;
    border-radius: 50%;
    background-color: rgb(179, 79, 79);
  }
  
  .resume-sub-heading {
    font-size: 14px;
   
    color: black;
    margin: 10px 0 0 0;
  }
  
  .resume-heading-description {
    font-size: 12px;
    text-align: justify;
  }
  
  .heading-date {
    background-color: rgb(179, 79, 79);
    padding: 4px 12px;
    font-size: 14px;
    border-radius: 14px;
    color: white;
  }
  
  .skill-parent {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 40%;
    margin-left: 3rem;
  }
  
  .programming-skills-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  

  .skill-parent span {
    
    font-size: 16px;
    color: black;
  }
  
  /* IPAD PRO RESPONSIVENESS */
  @media only screen and (max-width: 1110px) {
    .resume-card {
      width: 880px;
    }
  
    .resume-bullet-details {
      padding: 0 0 0 70px;
    }
  }
  
  /* IPAD RESPONSIVENESS */
  @media only screen and (max-width: 1023px) {
    .resume-content {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .bullets {
      width: 90%;
    }
  
    .resume-card {
      height: fit-content;
      display: flex;
      flex-direction: column;
      margin: 30px 0;
      align-items: center;
      width: 90%;
    }
  
    .resume-bullet-details {
      height: 360px;
      padding: 0;
      width: fit-content;
    }
  
    .resume-bullets {
      margin: 30px 0;
      width: 100%;
    }
  
    .selected-bullet {
      width: 100%;
    }
  }
