.central-form {
  max-width: 1100px;
  padding: 10px;
  display: flex;
  box-shadow: 0 0px 20px -2px rgb(179, 79, 79);
  border-radius: 12px;
  margin: 0 auto;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(179, 79, 79);
}

.central-form h2 {
  color: #fff;

  letter-spacing: 0.2rem;
  margin-bottom: 20px;
}
@media screen and (max-width: 414px) {
  .central-form h2 {
    font-size: 16px;
  }
}
.back-form {
  max-width: 380px;
  margin: 0 auto;
}

.back-form .img-back {
  width: 100%;
  margin-bottom: 20px;
}

.img-back h4 {
  letter-spacing: 0.3rem;
  font-weight: 400;
  color: white;
  font-size: 18px;
  opacity: 0.5;
}

.back-form .img-back img {
  width: 100%;
  opacity: 0.5;
}

.central-form form {
  width: auto;
  box-shadow: 0 0 2px 1px white;
  display: flex;
  color: #111;
  padding: 15px;
  justify-content: center;
  flex-direction: column;
  margin-top: -52px;
  opacity: 0.9;
  background-color: white;
  box-shadow: 0 0px 20px -2px #1f2235;
  border-radius: 12px;
}

form p {
  text-align: center;
  margin-bottom: 10px;
  color: red;
  letter-spacing: 0.1rem;
  
}

form label {
  margin-bottom: 5px;
  color: #111;
  letter-spacing: 0.2rem;
}

form input,
textarea {
  width: 92%;
  border: 2px solid rgba(0, 0, 0, 0);
  outline: none;
  background-color: rgba(230, 230, 230, 0.6);
  padding: 0.5rem 1rem;
  font-size: 1.1rem;
  margin-bottom: 22px;
  transition: 0.3s;
  border-radius: 10px;
}
form input::placeholder{
    font-size: 12px;
}
form textarea::placeholder{
    font-size: 12px;
}
textarea:focus {
  border: 2px solid rgb(179, 79, 79, 0.47);
  background-color: #fff;
}
form input:focus {
  border: 2px solid rgb(179, 79, 79, 0.47);
  background-color: #fff;
}

button .load {
  width: 100%;
}

button .load img {
  width: 60px;
  color: green;
}

@media screen and (min-width: 940px) {
  .back-form {
    max-width: 1100px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }

  .img-back {
    flex: 1;
    align-self: center;
    margin-right: 20px;
  }

  form {
    flex: 0.9;
  }
}
